* {
  box-sizing: border-box; }

html {
  scroll-behavior: smooth; }

body {
  font-family: "Gilroy-Medium", sans-serif;
  margin: 0px;
  font-size: 16px;
  height: 100%;
  color: #ffffff; }

h1 {
  font-size: 3rem;
  line-height: 3.625rem;
  font-family: "Gilroy-Bold", sans-serif;
  color: #ffffff; }

h2 {
  font-size: 2.5rem;
  line-height: 48px;
  font-family: "Gilroy-Bold", sans-serif;
  color: #081827;
  letter-spacing: -0.5px; }
  @media only screen and (max-width: 767px) {
    h2 {
      font-size: 2rem; } }

h3 {
  font-size: 2rem;
  font-family: "Gilroy-Bold", sans-serif;
  line-height: 38px;
  color: #081827; }
  @media only screen and (max-width: 767px) {
    h3 {
      font-size: 1.6rem; } }

h4 {
  font-size: 1.5rem;
  line-height: 32px;
  font-family: "Gilroy-Bold", sans-serif;
  letter-spacing: -0.1px;
  color: #081827; }

h5 {
  font-size: 20px;
  line-height: 28px;
  color: #081827;
  font-family: "Gilroy-Bold", sans-serif; }

strong, b, .bold {
  font-family: "Gilroy-Bold", sans-serif; }

.content {
  position: relative;
  padding-top: 4rem;
  padding-bottom: 4rem; }

.bg-overlay {
  background-color: #D1A684;
  opacity: 0.8;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0; }

img {
  max-width: 100%;
  display: block;
  height: auto; }

.shadow {
  box-shadow: 0px 4px 54px rgba(170, 170, 170, 0.25); }

.top-shadow {
  box-shadow: inset 0px 24px 19px -15px rgba(170, 170, 170, 0.25); }

.white-color {
  color: #ffffff; }

.primary-color {
  color: #ffffff; }

.primary-background {
  background-color: #ffffff;
  color: #ffffff; }
  .primary-background .content-header, .primary-background .preview-font {
    color: #ffffff; }

.off-white-background {
  background-color: #F6F6F6; }

.secondary-color {
  color: #081827; }

.secondary-background {
  background-color: #081827; }

.white-background {
  background-color: #ffffff; }

